.navbar {
  display: flex;
  width: 100vw;
  padding: 30px;
  justify-content: center;
}

.nav-wrapper {
  widows: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  max-width: 130px;
  height: 100%;
}

.logo img {
  width: 100%;
  margin-left: -18px;
}

.list-wrapper i {
  opacity: 0;
  z-index: 4;
  width: 40px;
  right: 20px;
  top: -50px;
  padding: 8px;
  position: absolute;
  border-radius: 5px;
  color: rgb(0, 119, 255);
  transition: all 0.5s ease-in-out;
}

.list-wrapper ul {
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 86vh;
  left: -100vw;
  padding-top: 140px;
  text-align: center;
  position: absolute;
  transition: all 0.5s ease-in-out;
  background-color: rgb(0, 0, 0, 0.8);
}

.list-wrapper ul li {
  list-style: none;
  margin-bottom: 15px;
}

.list-wrapper ul li a {
  display: block;
  font-size: 24px;
  padding: 10px 0;
  color: #dddddd;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.list-wrapper ul li a:hover {
  color: #000000;
  background-color: #dddddd;
}

@media ( min-width: 800px ) {
  .list-wrapper i {
    display: none; 
  }

  .list-wrapper ul {
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: left;
    position: relative;
    background-color: transparent;
  }

  .list-wrapper ul li {
    display: inline-block;
    margin-bottom: 0;
  }

  .list-wrapper ul li:not(:last-child) {
    margin-right: 10px;
  }

  .list-wrapper ul li a {
    padding: 5px;
    color: #000000;
  }

  .list-wrapper ul li a:hover {
    padding: 5px;
    color: #333333;
    transform: scale(0.8);
    background: transparent;
  }
}